$circle-diam: 30px;

%standart-line {
  width: 15px;
  border-radius: 10px;
}

%trans-origin {
  transform-origin: bottom center;
}

@keyframes tic-tic {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(1turn);
  }
}

body {
  display: flex;
  
  height: 100vh;
  
  align-items: center;
  justify-content: center;
}

.clock {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;

  &__minutes-container {
    position: absolute;
  
    top: -295px;
    bottom: 0;
    left: 0;
    right: 0;
  }

  &__minutes {
    @extend %standart-line;
    @extend %trans-origin;
  
    height: 300px;
    background-color: #d15559;
  
    animation: tic-tic 60s linear infinite;
  }

  &__seconds-container {
    position: absolute;
    
    top: -395px;
    bottom: 0;
    left: 0;
    right: 0;
  }

  &__seconds {
    @extend %standart-line;
    @extend %trans-origin;
    
    height: 400px;
  
    background-color: #59a0b0;
  
    animation: tic-tic 1s linear 0s infinite;
  
  }

  &__blue-circle {
    position: absolute;
  
    width: $circle-diam;
    height: $circle-diam;
    
    top: -5px;
    left: -7.5px;
  
    border-radius: 100%;
    background-color: #59a0b0;
  }
  
}
